





















import Vue from 'vue';

export default Vue.extend({
  name: 'DeploymentPicker',

  props: {
    value: {
      type: Object,
      default: null,
    },
    deployments: {
      type: Array,
      required: true,
    },
    isStartingRun: {
      type: Boolean,
      required: true,
    },
  },
});
