



























import Vue from 'vue';

import ExectutionLogItem from './ExecutionLogItem.vue';

export default Vue.extend({
  name: 'ExecutionLogTable',

  props: {
    logs: {
      type: Array,
      required: true,
    },
  },

  components: {
    ExectutionLogItem,
  },

  data() {
    return {
      headers: [
        { text: 'Type', value: 'type', width: '10%' },
        { text: 'Date', value: 'date', width: '10%' },
        { text: 'Time', value: 'time', width: '10%' },
        { text: 'Title', value: 'title', width: '20%' },
        { text: 'Details', value: 'detail', width: '50%' },
      ],
      options: {
        sortBy: ['id'],
        sortDesc: [true],
      },
      expanded: [],
    };
  },
});
